@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.converter__container{display:flex;flex-direction:column;box-sizing:border-box;padding:25px 10px;align-items:center;justify-content:center}.converter__label{display:flex;flex-direction:column;font-size:18px}.converter__input{margin-top:20px;height:30px;width:300px;font-size:16px}.converter__btn{box-sizing:border-box;padding:15px 40px;cursor:pointer;margin-top:40px;text-decoration:none;border:0;font-size:18px;background-color:#80f0af}.converter__btn:hover{background-color:#52eb92}.converter__image{max-height:50vh;max-width:90vw}*{font-family:'Noto Sans Mono', monospace}.App{font-family:'Noto Sans Mono', monospace;text-align:center}

